  }
  html{
    scroll-behavior: smooth;
  }
  .Nav_Container{
   width: 100%;
    background-color:#1b2430;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 80px ;
    box-shadow: 0 5px 15px rgba(0,0,0,0.06);
   z-index: 25;
  }
  #navitems{
    display: flex;
    gap:15px;
    justify-content: center;
    align-items: center;
  }
  .Nav_Container li{
    list-style: none;
    padding: 0 20px;
    position: relative;
  }
  .Nav_Container li a{
    text-decoration: none;
    font-size: 1.3rem;
    font-weight: 600;
    color: white;
    transition: 0.3s ease-in-out;
  }
  .Nav_Container li a:hover{
    color: #17cf97;
  }
  .Nav_Container li a:hover::after{
    content: "";
    width: 60%;
    height: 2px;
    background:  #17cf97;
    position: absolute;
    bottom: -5px;
    left: 20px;
  }
