  }
  .box::before{
    content: '';
    position: absolute;
    width:150px ;
    height: 1000px;
    background: linear-gradient(#00ccff,#d400d4);
    animation:  animate 4s linear infinite;
  
  }
  @keyframes animate {
    0%{
  transform: rotate(0deg);
    }100%{
      transform: rotate(360deg);
    }
  }
  .box::after{
    content: '';
    position: absolute;
    inset: 4px;
    color:#000000;
    background:white;
    border-radius: 16px;
    padding: 1px;
  }
  
