  .cube{
    position: relative;
    width:80%;
    height: 80%;
    transform-style: preserve-3d;
    animation: animate2 5s linear infinite;
  }
  @keyframes animate2 {
    0%{
      transform: rotateX(-30deg) rotateY(0deg);
    }
    100%{
      transform: rotateX(-30deg) rotateY(360deg);
    }
  }
  .cube div{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
  
  }
  .cube div span{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(#151515#1b2430);
    transform:translate4D(calc(3em + 2px), calc(3em + 2px), 201px);
    transform: rotateY(calc(90deg * var(--i))) translateZ((150px));
    transform-style: preserve-3d;
  
  }
  .cube div span img{
    width: 100%;
    border-radius: 15px;
  }
