      font-size: calc(30px + 6 * ((100vw - 320px) / 680));
      font-weight: bold;
    }
    .intro>div>h2{
      color:yellow;
      font-size: 25px;
      font-weight: bold;
    }
  
    .skillsimg{
      display: grid;
      grid-template-columns: repeat(2,45%);
      justify-content: center;
      align-items: center;
    }
    .skillsbox{
      width:98%;
      height: 250px ;
     
      
      }
      .showbox{
        display: grid;
        grid-template-columns: 90%;
        gap: 10px;
        justify-content: center;
        align-items: center;
        
      }
      .projectbox{
        width: 100%;
        height: auto ;
       justify-content: center;
       align-items: center;
       padding: 10px;
  
        
        }
        .hideform{
          display:none;
  
        }
        .contact1{
          margin: auto;
          width: 90%;
        }
        .stats{
          width:90%;
          height: auto;
          padding: 10px;
        }
        .form{
          width: 200%;
          padding: 10px;
      margin-left: 25px;
          
        }
        
        input{
          width: 90%;
        }
        .github{
          display: flex;
          justify-content: center;
  
          
        }
        .projectbox>div:nth-child(1){
         
          margin-top: 0px;
         
        }
        .projectbox>div:nth-child(2){
          text-align: justify;
          margin-top: 10px;
          font-size: 15px;
          padding: 5px;
         }
         
         .contactdetail{
          display: grid;
          grid-template-columns:100%;
          gap: 5px;
          width: 100%;
          justify-content: center;
          align-items: center;
          margin: auto;
  
          }
  .button-22{
    width: 100%;
    height: 100px;
  }
          .contactdetail>div{
          
          display: flex;
          justify-content: center;
          align-items: center;
          }
          .bottom{
            margin-top: 5px;
            height: 30px;
            font-size: 8px;
          }
          .box1 img{
            position: relative;
           width: 100%;
           
            
            text-align: justify;
            border-radius: 50%;
          }
          .box1::before{
            content: '';
            position: absolute;
            width:200px ;
            height: 1000px;
            background: linear-gradient(#05ee05,#d400d4);
            animation:  animate1 4s linear infinite;
         
          }
  
          .imagebox{
            z-index: 9;
            border-radius: 60%;
            box-shadow: inset 0px -50px 60px -20px yellow;
            
            }
            .imagebox img{
              position:relative ;
              height: 90%;
              width: auto;
             display: flex;
             justify-content: flex-end;
             top:-10px;
             right: 50px;
             margin-left: 50px;
              animation:  animate5 4s linear infinite;
            
            }
            .gradient{
              font-size: 35px;
             font-weight: bold;
            }
  
